#about-masthead {
  display: flex;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.about-container {
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  visibility: hidden;
}

.page {
  background: #fff;
}

.block_hero h1 {
  font-size: 5rem;
  font-weight: 600;
  letter-spacing: 0.1rem;
  margin-top: 0;
  flex: 1;
}

.about-banner--bg-grey {
  background: #cbd1d2;
}

.line-wrap {
  overflow: hidden;
  height: 56px;
}

.about-followup h2 {
  font-size: 4rem;
  font-weight: 200;
}

.about-followup p {
  font-size: 1.625rem;
  line-height: 2.5rem;
}

.h1-desig {
  display: inline-block;
  position: relative;
  color: #731010;
  line-height: 5rem;
}

.h1-desig::after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 50%;
  height: 1rem;
  background: #b9acca;

  left: 5px;
  bottom: -20%;
}

.block_hero p {
  margin-top: 3rem;
  margin-bottom: 3rem;

  font-size: 1.625rem;
  font-weight: 400;
  line-height: 26px;
}

.about-hero__text {
  flex: 0 0 60%;
  display: flex;
  flex-direction: column;
}

.image-container {
  align-self: flex-start;
  width: 40%;
  position: relative;
  overflow: hidden;
  height: 520px;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #ebe8e6;
    background: #fff;
  }
}

.about-hero__img {
  width: 100%;
  height: 40rem;
  object-fit: cover;
}

.image-container-2i {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
}

.about-hero__img-1 {
  width: 40%;
  height: 40rem;
  object-fit: cover;
  margin-right: 6.8rem;
}

.about-hero__img-2 {
  position: absolute;
  top: 22%;
  width: 50%;
  height: 40rem;
  object-fit: cover;
}

.about-followup {
  width: 100%;
  height: 70%;
  display: block;
  position: relative;
  margin-top: 4rem;
}
.flex-based {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.about-comp .column {
  padding-top: 5vw;
}

.column {
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}

.img-responsive {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.column.r-one {
  width: 50%;
}

.column.r-two {
  width: 34%;
}

.column.r-two {
  margin-left: 5vw;
}

.about-comp .column {
  padding-top: 5.2vw;
}

.block_hero {
  width: 100%;
  display: block;
  position: relative;
  /* background: #fff; */
}

.block_hero-inner {
  position: relative;
  z-index: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: auto;
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
}

.ror-desc {
  flex: 0 0 60%;
  height: 100%;
}

.block_hero-img-about {
  flex: 0 0 40%;
  /* overflow: hidden; */
  object-fit: cover;
  height: 620px;
  /* max-height: 70vh; */
  background-color: transparent;
  background-image: url(https://uploads-ssl.webflow.com/5e3c4cb20c77519bae9c7017/5e7a3fce1de2797570d27826_hero-gubi.jpg);
  background-repeat: no-repeat;
  background-position: 100% 100%;
  background-size: auto 100%;
}

.h2 {
  max-width: 650px;
  margin: 0 auto 24px 0;
  border: 1px #fff;
  color: #111;
  font-size: 56px;
  line-height: 56px;
  font-weight: 600;
  text-align: left;
  letter-spacing: -2px;
}

.block-paragraph {
  max-width: 56rem;
  margin-bottom: 2.4rem;
  flex: 0 auto;
  color: #555;
  font-size: 1.6rem;
  line-height: 2.4rem;
  font-weight: 400;
  letter-spacing: 0;
}

#splash {
  margin-top: 2.8rem;
  display: flex;
}

#splash .splash-inner {
  position: relative;
}
#splash .splash-inner img {
  width: 100%;
  height: auto;
  position: relative;
  display: block;
}

.splash-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.box-letter {
  display: inline-block;
  text-align: center;
  font-weight: 200;
}

.box-letter span {
  font-size: var(--header2);
  line-height: 52px;
}

.splash-content p {
  font-size: var(--text);
}

@media screen and (min-width: 981px) {
  .about-banner--bg-grey.about-banner--img .about-banner__img:before {
    background: url('../../../assets/images/banner-shape-grey.svg') top
      left/auto 100% no-repeat;
  }
}

@media screen and (min-width: 981px) {
  .about-banner--img .about-banner__img:before {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    width: calc(100% + 2px);
    height: calc(100% + 2px);
  }
}
